import { $ } from '../UX/customeJQ/customeJQ';

export function debounce(f, time: number) {
  let lock = false;
  const thisFun = f as typeof Function;

  return function (...arg: any) {
    if (lock) {
      return;
    }

    thisFun.apply(thisFun, arg);
    lock = true;
    setTimeout(() => {
      lock = false;
    }, time);
  };
}

export function haveScrolled(f) {
  const thisFun = f as typeof Function;
  let scrolled = 0;

  return function (...arg: any) {
    const scrollTop = window.pageYOffset;
    const arg2 = arg.concat([scrolled, scrollTop]);
    thisFun.apply(thisFun, arg2);
    scrolled = scrollTop;
  };
}

export function handlerSideBar(e: Event, scrolled: number, scrollTop: number) {
  const scroll = window.pageYOffset + document.documentElement.clientHeight;
  const path = location.pathname;

  if (scrollTop > 100) {
    $('.header, .phone').add('show');
  } else {
    $('.header, .phone').remove('show');
  }

  if (e.type !== 'resize') {
    if (scrollTop > 200 && scrollTop >= scrolled && !path.includes('basket')) {
      $('.header').add('deactive');
    } else {
      $('.header').remove('deactive');
    }
  }

  if (scroll >= window.innerHeight / 2 - 300) {
    $('.bannerloc__container').add('deactive');
  } else {
    $('.bannerloc__container').remove('deactive');
  }
}

export function onScroll(scrolled: number) {
  const scroll = window.pageYOffset + document.documentElement.clientHeight;

  const elements = Array.from(document.querySelectorAll('.scroll'));

  for (const item of elements) {
    if ((item as HTMLElement)?.dataset.block) {
      if (scroll > item.getBoundingClientRect().top + window.pageYOffset) {
        item.classList.add('show');
      }
    } else if ((item as HTMLElement)?.dataset.gym) {
      if (scroll > item.getBoundingClientRect().top + window.pageYOffset) {
        item?.closest('.elem')?.classList.add('show');
      }
    } else if ((item as HTMLElement)?.dataset.footer) {
      // * goroof ========================================================

      const arrowgo = document.querySelector('.goroof') as HTMLElement;
      if (scroll >= item.getBoundingClientRect().bottom + window.pageYOffset) {
        arrowgo?.classList.add('show');
      } else {
        arrowgo?.classList.remove('show');
      }
      const arrowgotwo = document.querySelector('.footer') as HTMLElement;
      if (
        scroll >=
        item.getBoundingClientRect().bottom + window.pageYOffset + 20
      ) {
        arrowgotwo?.classList.add('show');
      } else {
        arrowgotwo?.classList.remove('show');
      }
      // * goroof - end =================================================
    } else if ((item as HTMLElement)?.dataset.sale) {
      const scrolltop = window.pageYOffset;

      const arrowgo = document.querySelector('.basket__price') as HTMLElement;
      const header = document.querySelector('.header') as HTMLElement;
      const headerHeight =
        (
          document.querySelector('.header') as HTMLElement
        ).getBoundingClientRect().bottom + scrolltop;
      const blockone = document.querySelector(
        '.productbasket__goods'
      ) as HTMLElement;
      const blocktwo = arrowgo.getBoundingClientRect();

      const block = (
        document.querySelector('.pageonebas') as HTMLElement
      ).getBoundingClientRect();

      if (
        +headerHeight.toFixed(0) <= +(block.top + scrolltop).toFixed(0) ||
        arrowgo.clientHeight >= blockone.clientHeight ||
        document.documentElement.clientWidth < 1420
      ) {
        arrowgo?.classList.remove('bottom');
        arrowgo?.classList.remove('show');
      } else if (
        //* toBottom
        headerHeight >= item.getBoundingClientRect().top + scrolltop &&
        scrolltop > scrolled &&
        +(block.bottom + scrolltop).toFixed(0) >
          +(blocktwo.bottom + scrolltop).toFixed(0)
      ) {
        arrowgo?.classList.add('show');
        arrowgo.style.top = getComputedStyle(header).height;
      } else if (
        //* toTop
        scrolltop < scrolled &&
        headerHeight <= blocktwo.top + scrolltop
      ) {
        arrowgo?.classList.remove('bottom');
        arrowgo?.classList.add('show');
        arrowgo.style.top = getComputedStyle(header).height;
      } else if (
        //* onBottom
        block.bottom + scrolltop <= blocktwo.bottom + scrolltop &&
        arrowgo.clientHeight + 10 < blockone.clientHeight
      ) {
        arrowgo?.classList.add('bottom');
        arrowgo?.classList.remove('show');
        arrowgo.style.top = 'auto';
      }
    }
  }
}
